import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '../store'
import { getToken } from '@/utils/auth'

// axios
const service = axios.create({
  // baseURL: process.env.BASE_API, // apibase_url
  baseURL: '/api/index.php/', // apibase_url
  timeout: 15000                  // 
})

// request
service.interceptors.request.use(config => {
  if (store.getters.token) {
      config.data['token'] = getToken() // token 
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => {
  /**
  * code200
  */
    const res = response.data
    if (res.code !== 200) {
      Message({
        message: res.msg,
        type: 'error',
        duration: 2 * 1000
      })

      // 401:
      if (res.code === 401) {
        // MessageBox.confirm('', '', {
        //   confirmButtonText: '',
        //   cancelButtonText: '',
        //   type: 'warning'
        // }).then(() => {
        //   store.dispatch('FedLogOut').then(() => {
        //     // location.reload()// vue-router bug
        //     window.location.assign(window.location.origin + window.location.pathname + '#/login');
        //   })
        // })
        store.dispatch('FedLogOut').then(() => {
          // location.reload()// vue-router bug
          window.location.assign(window.location.origin + window.location.pathname + '#/login');
        })
      }
      return Promise.reject(res);
    } else {
      return Promise.resolve(response.data.result);
    }
  },
  error => {
    console.log('err' + error)// for debug
    Message({
      message: error.msg,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service



